<template>
  <section class="container">
    <div class="blog-post-view">
      <h1>{{ post.title }}</h1>
      <p>{{ post.content }}</p>
      <router-link to="/blog">
        Go Back to Blog
      </router-link>
    </div>
  </section>
</template>

<script>
export default {
  name: 'BlogPost',
  data() {
    return {
      post: {}
    };
  },
  created() {
    const postId = this.$route.params.id;
    // Fetch the post data based on the ID (for simplicity, using static data here)
    const posts = [
      { id: 1, title: 'First Post', content: 'This is the content of the first post.' },
      { id: 2, title: 'Second Post', content: 'This is the content of the second post.' },
      // Add more posts here
    ];
    this.post = posts.find(post => post.id === parseInt(postId));
  }
};
</script>

<style scoped>
.blog-post-view {
  padding: 20px;
}

.blog-post-view h1 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.blog-post-view p {
  font-size: 1rem;
}
</style>