          <div class="col-source-text">
            <h3 class="text-color-2 animate-on-scroll">Bello Restaurant</h3>
            <p class="text-color-1 animate-on-scroll">Restaurant Bello is a restaurant located in Bihor Romania, this was a demonstration site of my skills, this was made in 2021</p>
