        </div>
      </div>
      <div class="wrap">
        <div class="col-grid-source">
          <div>
            <img @mousemove="handleMouseMove" @mouseleave="resetTransform" class="img-fluid animate-on-scroll img-source" src="../assets/pitch.png" alt="Pitch Image">
          </div>
          <div class="col-source-text">
            <h3 class="text-color-2 animate-on-scroll">Pitch</h3>
            <p class="text-color-1 animate-on-scroll">It is a front-end copy of the pitch site. Pitch is a tool for creating and sharing presentations with custom templates, AI drafts, and smart features.</p>
            <a class="text-color-1 btn-live-source animate-on-scroll" target="_blank" href="https://modest-rosalind-dec289.netlify.app/">Live</a>
          </div>
        </div>
      </div>
      <div class="wrap">
        <div class="col-grid-source cst-reverse">
          <div class="col-source-text">
            <h3 class="text-color-2 animate-on-scroll">Github Search</h3>
            <p class="text-color-1 animate-on-scroll"> Easily search for GitHub users by their username. View detailed information about each user, including their avatar, bio, location, and public repositories. Browse through the list of public repositories for each user, complete with links to the repositories on GitHub.</p>
            <a class="text-color-1 btn-live-source animate-on-scroll" target="_blank" href="https://mecheresvalentin.github.io/githubsearch/">Live</a>
          </div>
          <div>
            <img @mousemove="handleMouseMove" @mouseleave="resetTransform" class="img-fluid animate-on-scroll img-source" src="../assets/github.png" alt="Github Image">
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  mounted() {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('show');
        } else {
          entry.target.classList.remove('show');
        }
      });
    });

    const elements = document.querySelectorAll('.animate-on-scroll');
    elements.forEach(el => observer.observe(el));
  },
  methods: {
    handleMouseMove(event) {
      const img = event.target;
      const rect = img.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;
      const rotateX = (y / rect.height - 0.5) * 20; // Adjust the multiplier for more/less rotation
      const rotateY = (x / rect.width - 0.5) * -20; // Adjust the multiplier for more/less rotation
      img.style.transform = `rotateX(${rotateX}deg) rotateY(${rotateY}deg)`;
    },
    resetTransform(event) {
      event.target.style.transform = 'rotateX(0) rotateY(0)';
    }
  }
}
</script>
