<template>
  <div id="app">
    <nav class="navbar-sticky">
      <router-link v-scroll-to="{ el: '#home', offset: -120 }" to="/">
        <img height="40px" src="./assets/face.png" alt="Face Logo Nav" />
      </router-link>
      <ul :class="{ 'sidebar-open': sidebarOpen }">
        <li>
          <router-link v-scroll-to="{ el: '#home', offset: -120 }" to="/">
            Home
          </router-link>
        </li>
        <li>
          <router-link v-scroll-to="{ el: '#home', offset: -120 }" to="/charitydev">
            Charitable Dev
          </router-link>
        </li>
        <li>
          <router-link v-scroll-to="{ el: '#home', offset: -120 }" to="/blog">
            Blog
          </router-link>
        </li>
      </ul>
      <div :class="['burger-menu', { 'open': sidebarOpen }]" @click="toggleSidebar">
        <div class="line"></div>
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </nav>
    <transition name="fade" mode="out-in">
      <router-view />
    </transition>
    <FooterSec/>
    <div class="custom-cursor" ref="cursor"></div>
    <div class="progress-bar" :style="{ width: scrollProgress + '%' }"></div>
    <button v-show="showBackToTop" @click="scrollToTop" class="back-to-top"><i class="fa-solid fa-arrow-up-long"></i></button>
  </div>
</template>

<script>
import FooterSec from '@/components/FooterSec.vue'

export default {
  name: "HomeView",
  components: {
    FooterSec
  },
  data() {
    return {
      scrollProgress: 0,
      showBackToTop: false,
      sidebarOpen: false,
    };
  },
  mounted() {
    window.addEventListener('scroll', this.updateScrollProgress);
    window.addEventListener('scroll', this.checkScrollPosition);
    window.addEventListener('mousemove', this.updateCursorPosition);
    const links = document.querySelectorAll('a');
    links.forEach(link => {
      link.addEventListener('mouseenter', this.enlargeCursor);
      link.addEventListener('mouseleave', this.shrinkCursor);
    });
    window.addEventListener('resize', this.handleResize);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.updateScrollProgress);
    window.removeEventListener('scroll', this.checkScrollPosition);
    window.removeEventListener('mousemove', this.updateCursorPosition);
    const links = document.querySelectorAll('a');
    links.forEach(link => {
      link.removeEventListener('mouseenter', this.enlargeCursor);
      link.removeEventListener('mouseleave', this.shrinkCursor);
    });
  },
  methods: {
    toggleSidebar() {
      this.sidebarOpen = !this.sidebarOpen;
    },
    handleResize() {
      if (window.innerWidth > 768) {
        this.sidebarOpen = false;
      }
    },
    updateScrollProgress() {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      const scrollHeight = document.documentElement.scrollHeight - document.documentElement.clientHeight;
      this.scrollProgress = (scrollTop / scrollHeight) * 100;
    },
    checkScrollPosition() {
      this.showBackToTop = window.pageYOffset > 300;
    },
    scrollToTop() {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    },
    updateCursorPosition(event) {
      const cursor = this.$refs.cursor;
      cursor.style.left = `${event.clientX}px`;
      cursor.style.top = `${event.clientY}px`;
    },
    enlargeCursor() {
      const cursor = this.$refs.cursor;
      cursor.classList.add('cursor-enlarged');
    },
    shrinkCursor() {
      const cursor = this.$refs.cursor;
      cursor.classList.remove('cursor-enlarged');
    }
  }
}
</script>

